import ProjectGrid from './components/ProjectGrid'

export default function PortfolioPage() {
  return (
    <div className="min-h-screen bg-gradient-to-br from-white via-blue-50/50 to-purple-50/50 dark:from-gray-900 dark:via-primary/5 dark:to-secondary/5">
      <div className="container mx-auto px-6 py-24">
        <h1 className="text-4xl md:text-5xl font-bold mb-16 font-geist-sans bg-clip-text text-transparent bg-gradient-to-r from-gray-900 to-gray-600 dark:from-white dark:to-gray-200">
          Portfolio
        </h1>
        <div className="relative">
          <div className="absolute -inset-4 bg-gradient-to-r from-primary to-purple-600 rounded-2xl blur opacity-10"></div>
          <div className="relative bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm rounded-xl p-8 border border-gray-200 dark:border-gray-700">
            <ProjectGrid />
          </div>
        </div>
      </div>
    </div>
  )
} 